import React, {Suspense}from "react";
import Example from "../core/popup";
import InfiniteScroll from "react-infinite-scroller";
import { isAuthenticated } from "../auth";
import NewPost from "../post/NewPost"
/* import Posts from "../post/Post" */
const Posts = React.lazy(() => import("../post/Post")); // Lazy-loaded

const Home = () => (
  
  <div className="ui container">
  {!isAuthenticated() && (<div><h2>Join us <a href="/signup">now!</a></h2></div>)}
  {isAuthenticated() && (<div><NewPost></NewPost></div>)}
  <hr></hr>
  
  <Suspense fallback={<div>Loadingsssss</div>}>
  <InfiniteScroll>
  <Posts></Posts>
  </InfiniteScroll>
</Suspense>

  </div>
);
export default Home;
