import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Image, Feed, Tab, Icon } from "semantic-ui-react";
import DefaultProfile from "./defauly.jpg";
import Followers from "./Followers";

class ProfileTabs extends Component {
  render() {
    const panes = [
      {
        menuItem: "Posts",
        render: () => <Tab.Pane attached={false}><PostsByUser></PostsByUser></Tab.Pane>,
      },
      {
        menuItem: "Followers",
        render: () => (
          <Tab.Pane attached={false}>
            <Followers></Followers>
          </Tab.Pane>
        ),
      },
      {
        menuItem: "Following",
        render: () => (
          <Tab.Pane attached={false}>
            <Following></Following>
          </Tab.Pane>
        ),
      },
    ];

    const { following, followers, posts } = this.props;
    const TabExampleCustomMenuItem = () => <Tab panes={panes} />;
    const Following = () => (
      <div>
        {following.map((person, i) => {
          return (
            <Image.Group size="tiny" style={{ display: "inline" }}>
              <Image
                as="a"
                href={`/user/${person._id}`}
                style={{
                  mainHeight: "50px",
                  maxHeight: "50px",
                  mainWidth: "50px",
                  maxWidth: "50px",
                }}
                onError={(i) => (i.target.src = `${DefaultProfile}`)}
                src={`http://localhost/api/user/photo/${person._id}`}
                circular
              />
            </Image.Group>
          );
        })}
      </div>
    );
    const PostsByUser = () => (
      <div>
        {posts.map((post, i)=> {
          
          return (
            <div> <Feed>
          {posts.map((post, i) => (
            <Feed.Event key={i}>
              <Feed.Label
                style={{
                  backgroundColor: "white",
                  width: "70px",
                  height: "70px",
                }}
              >
                <Image
                  fluid
                  circular
                  as="a"
                  href={`/user/${post.postedBy._id}`}
                  style={{
                    mainHeight: "60px",
                    maxHeight: "60px",
                    mainWidth: "60px",
                    maxWidth: "60px",
                  }}
                  src={`http://localhost/api/user/photo/${post.postedBy._id}`}
                  onError={(i) => (i.target.src = `${DefaultProfile}`)}
                ></Image>
              </Feed.Label>
              <Feed.Content>
                <Feed.Summary>
                  <a>{post.postedBy.name}</a>
                  <Feed.Date>{new Date(post.created).toDateString()}</Feed.Date>
                </Feed.Summary>
                <Feed.Extra text>{post.body}</Feed.Extra>
                <Feed.Extra Image>
                  {" "}
                  <img
                                    style={{
                    mainHeight: "300px",
                    maxHeight: "300px",
                    mainWidth: "300px",
                    maxWidth: "300px",
                  }}
                    src={`http://localhost/api/post/photo/${post._id}`}
                    onError=""
                    alt=""

                    
                  ></img>
                </Feed.Extra>
                <Feed.Meta>
                  <Feed.Like>
                    <Icon name="like" />5 Likes
                  </Feed.Like>
                </Feed.Meta>
                <hr></hr>
              </Feed.Content>
            </Feed.Event>
          ))}
        </Feed></div>
          )
        })}
      </div>
    )
    const Followers = () => (
      <div>
      
        {followers.map((person, i) => {
          return (
            <Image.Group size="tiny" style={{ display: "inline" }}>
              <Image
                as="a"
                href={`/user/${person._id}`}
                style={{
                  mainHeight: "50px",
                  maxHeight: "50px",
                  mainWidth: "50px",
                  maxWidth: "50px",
                }}
                onError={(i) => (i.target.src = `${DefaultProfile}`)}
                src={`http://localhost/api/user/photo/${person._id}`}
                circular
              />
            </Image.Group>
          );
        })}
      </div>
    );
    return (
      <div>
        <TabExampleCustomMenuItem></TabExampleCustomMenuItem>
      </div>
    );
  }
}
export default ProfileTabs;
