import React, { Component } from "react";
import { isAuthenticated } from "../auth";
import { Redirect, Link } from "react-router-dom";
import { listByUser, list } from "../post/apiPost";
import {
  Container,
  Header,
  Grid,
  Image,
  Button,
  Icon,
  Card,
} from "semantic-ui-react";
import { read } from "./apiuser";
import Deleteuser from "./Delteuser";
import DefaultProfile from "./defauly.jpg";
import FollowProfileButton from "./FollowProfileButton";
import DeleteUser from "./Delteuser";
import ProfileTabs from "./ProfileTabs";
import HeaderSubHeader from "semantic-ui-react/dist/commonjs/elements/Header/HeaderSubheader";

class Profile extends Component {
  constructor() {
    super();
    this.state = {
      user: { following: [], followers: [] },
      following: false,
      readirectToSignin: false,
      error: "",
      posts: [],
    };
  }
  ///followCheaker
  cheakFollow = (user) => {
    const jwt = isAuthenticated();
    const match = user.followers.find((follower) => {
      return follower._id === jwt.user._id;
    });
    return match;
  };

  clickFollowButton = (callApi) => {
    const userId = isAuthenticated().user._id;
    const token = isAuthenticated().token;

    callApi(userId, token, this.state.user._id).then((data) => {
      if (data.error) {
        this.setState({ error: data.error });
      } else {
        this.setState({ user: data, following: !this.state.following });
      }
    });
  };

  init = (userId) => {
    const token = isAuthenticated().token;
    read(userId, token).then((data) => {
      if (data.error) {
        this.setState({ readirectToSignin: true });
      } else {
        let following = this.cheakFollow(data);
        this.setState({ user: data, following });
        this.LoadPosts(data._id);
      }
    });
  };

  LoadPosts = (userId) => {
    const token = isAuthenticated().token;
    listByUser(userId, token).then((data) => {
      if (data.error) {
        console.log(data.error);
      } else {
        this.setState({ posts: data });
      }
    });
  };
  componentDidMount() {
    const userId = this.props.match.params.userId;
    this.init(userId);
  }
  componentWillReceiveProps(props) {
    const userId = props.match.params.userId;
    this.init(userId);
  }

  render() {
    const { readirectToSignin, user, posts } = this.state;
    const photoUrl = user._id
      ? `http://localhost/api/user/photo/${user._id}`
      : DefaultProfile;
    if (readirectToSignin) return <Redirect to="/signin"></Redirect>;
    return (
      <Container text style={{ marginTop: "10vh" }}>
        <Header as="h2" icon textAlign="center">
          <Image
            as="a"
            href={`http://localhost/api/user/photo/${user._id}`}
            fluid
            circular
            style={{
              mainHeight: "60px",
              maxHeight: "60px",
              mainWidth: "60px",
              maxWidth: "60px",
            }}
            src={photoUrl}
            onError={(i) => (i.target.src = `${DefaultProfile}`)}
          />{" "}
          <Header.Content>{user.name}</Header.Content>
          <HeaderSubHeader style={{ fontSize: "20px" }}>
            <p>{user.about}</p>
          </HeaderSubHeader>
          <Header.Subheader>
            {" "}
            <p style={{ fontSize: "10px" }}>{`Joined ${new Date(
              user.created
            ).toDateString()}`}</p>
          </Header.Subheader>
          {isAuthenticated().user && isAuthenticated().user._id === user._id ? (
            <div className="d-inline-block" style={{ float: "right" }}>
              <DeleteUser></DeleteUser>
              <Link to={`/user/edit/${this.state.user._id}`}>
                <Button>Edit Profile</Button>
              </Link>
            </div>
          ) : (
            <FollowProfileButton
              following={this.state.following}
              onButtonClick={this.clickFollowButton}
            />
          )}
        </Header>
        <hr></hr>
        <ProfileTabs
          followers={user.followers}
          following={user.following}
          posts={posts}
        ></ProfileTabs>
      </Container>
    );
  }
}

export default Profile;
