import React, { Component } from "react";
import { isAuthenticated } from "../auth";
import { Icon, TextArea } from "semantic-ui-react";
import { create } from "../post/apiPost";
import { toast, ToastContainer } from "react-toastify";
import { Redirect } from "react-router-dom";

/* import { Container, Header, Grid, Image, Button } from "semantic-ui-react"; */

class NewPost extends Component {
  constructor() {
    super();
    this.state = {
      title: "",
      body: "",
      photo: "",
      error: "",
      user: {},
      fileSize: 0,
      loading: false,
    };
  }

  componentDidMount() {
    this.postData = new FormData();
    this.setState({ user: isAuthenticated().user });
  }

  isValid = () => {
    const { body, fileSize } = this.state;
    if (fileSize > 50000000) {
      toast.error("Photo size is too large")
      return false;
    }
    if (body.length == 0) {
      toast.error("What do you wanna say?")
      return false;
    } else {
      return true;
    }
  };

  handleChange = (name) => (event) => {
    this.setState({ error: "" });
    const value = name === "photo" ? event.target.files[0] : event.target.value;
    const fileSize = name === "photo" ? event.target.files[0].size : 0;
    this.postData.set(name, value);
    this.setState({ [name]: value, fileSize });
  };

  clickSubmit = (event) => {
    event.preventDefault();
    if (this.isValid()) {
      const userId = isAuthenticated().user._id;
      const token = isAuthenticated().token;

      create(userId, token, this.postData).then((data) => {
        if (data.error) {
          toast.error(this.state.error);
        } else {
          this.json((window.location.href = "/"));
        }
      });
    }
  };

  render() {
    const { title, body, photo, user, error, loading } = this.state;
    /*     if (readirectToProfile) {
      return <Redirect to={`/user/${id}`}></Redirect>;
    }
 */

    const upload = {
      position: "relative",
      display: "inline-block",
      borderRadius: "8px;",
      border: "#ebebeb solid 1px",
      width: "32px",
      padding: " 4px 6px 4px 8px",
      font: "normal 14px Myriad Pro, Verdana, Geneva, sans-serif",
      color: "#7f7f7f",
      marginTop: "2px",
      background: "white",
    };

    const uploadInput = {
      webkitAppearance: "none",
      position: "absolute",
      top: "0",
      left: "0",
      opacity: "0",
    };

    /* const photoUrl = id ? `http://localhost/user/photo/${id}` : DefaultProfile; */
    return (
      <div className="ui container">
        <ToastContainer></ToastContainer>
        <h2>What's going on?</h2>
        <form className="ui form">
          <TextArea
            placeholder="Tell us more"
            onChange={this.handleChange("body")}
            value={body}
          ></TextArea>{" "}
          <div style={upload}>
            <Icon name="photo"></Icon>
            <input
              style={uploadInput}
              onChange={this.handleChange("photo")}
              className="first-name"
              placeholder="First Name"
              accept="image/*"
              type="file"
            />
          </div>
          <div className="field"></div>
          <button
            onClick={this.clickSubmit}
            className="ui button"
            type="submit"
          >
            Post
          </button>
        </form>
      </div>
    );
  }
}

export default NewPost;
