import React, { Component } from "react";
import { isAuthenticated } from "../auth";
import { read, update, updateUser } from "./apiuser";
import { toast, ToastContainer } from "react-toastify";
import { Redirect } from "react-router-dom";
import DefaultProfile from "./defauly.jpg";
/* import { Container, Header, Grid, Image, Button } from "semantic-ui-react"; */

class Editprofile extends Component {
  constructor() {
    super();
    this.state = {
      id: "",
      name: "",
      error: "",
      password: "",
      readirectToProfile: false,
      fileSize: 0,
      about: "",
    };
  }
  init = (userId) => {
    const token = isAuthenticated().token;
    read(userId, token).then((data) => {
      if (data.error) {
        toast.error(data.error);
        this.setState({ error: data.error, readirectToProfile: false });
      } else {
        this.setState({
          id: data._id,
          name: data.name,
          email: data.email,
          error: "",
          about: data.about,
        });
      }
    });
  };

  componentDidMount() {
    this.userData = new FormData();
    const userId = this.props.match.params.userId;
    this.init(userId);
  }

  isValid = () => {
    const { email, name, password, fileSize } = this.state;
    if (fileSize > 2000000) {
      this.setState({ error: "Image File shoud be less than 2MB" });
      return false;
    }
    if (name.length == 0) {
      this.setState({ error: "Name is required" });
      return false;
    } else {
      return true;
    }
  };

  handleChange = (name) => (event) => {
    this.setState({ error: "" });
    const value = name === "photo" ? event.target.files[0] : event.target.value;
    const fileSize = name === "photo" ? event.target.files[0].size : 0;
    this.userData.set(name, value);
    this.setState({ [name]: value, fileSize });
  };

  clickSubmit = (event) => {
    if (this.isValid()) {
      event.preventDefault();
      const userId = this.props.match.params.userId;
      const token = isAuthenticated().token;

      update(userId, token, this.userData).then((data) => {
        if (data.error) toast.error(data.error);
        updateUser(data, () => {
          this.setState({
            readirectToProfile: true,
          });
        });
      });
    }
  };

  render() {
    const {
      name,
      id,
      email,
      password,
      readirectToProfile,
      error,
      about,
    } = this.state;
    if (readirectToProfile) {
      return <Redirect to={`/user/${id}`}></Redirect>;
    }

    const photoUrl = id ? `http://localhost/user/photo/${id}` : DefaultProfile;
    return (
      <div className="ui container">
        <ToastContainer></ToastContainer>
        <div className="ui medium circular image">
          <img
            src={photoUrl}
            onError={(i) => (i.target.src = `${DefaultProfile}`)}
          ></img>
        </div>
        <h3 className="ui block header">Block Header</h3>
        <h2 className="mt-5 mb*5">Profile Photo</h2>
        <form className="ui form">
          <div className="field">
            <label>ProfilePhoto</label>

            <input
              onChange={this.handleChange("photo")}
              className="first-name"
              placeholder="First Name"
              accept="image/*"
              type="file"
            ></input>
          </div>
          <div className="field">
            <label>Full Name</label>
            <input
              onChange={this.handleChange("name")}
              className="first-name"
              placeholder="First Name"
              value={name}
            ></input>
          </div>
          <div className="field">
            <label>Description</label>
            <textarea
              onChange={this.handleChange("about")}
              className=""
              value={about}
            ></textarea>
          </div>
          <div className="field">
            <label>Email</label>
            <input
              onChange={this.handleChange("email")}
              type="email"
              className="last-name"
              placeholder="Last Name"
              value={email}
            ></input>
          </div>
          <div className="field">
            <label>Password</label>
            <input
              onChange={this.handleChange("password")}
              type="password"
              className="last-name"
              placeholder="Last Name"
              value={password}
            ></input>
          </div>
          <div className="field"></div>
          <button
            onClick={this.clickSubmit}
            className="ui button"
            type="submit"
          >
            Update
          </button>
        </form>
      </div>
    );
  }
}

export default Editprofile;
