import React, { Component } from "react";
import PeopleToFollow from "./Findpeople";
import DefaultProfile from "./defauly.jpg";
import { follow } from "./apiuser";
import { isAuthenticated } from "../auth";
import {
  Container,
  Header,
  Grid,
  Image,
  Tab,
  Card,
  Icon,
  CardContent,
} from "semantic-ui-react";
import { list } from "./apiuser";
class Users extends Component {
  render() {
    const TabExampleCustomMenuItem = () => <Tab panes={panes} />;
    const panes = [
      {
        menuItem: "AllMeowrs",
        render: () => (
          <Tab.Pane attached={false}>
            <AllUsers></AllUsers>
          </Tab.Pane>
        ),
      },
      {
        menuItem: "Meowers to follow",
        render: () => (
          <Tab.Pane attached={false}>
            <PeopleToFollow></PeopleToFollow>
          </Tab.Pane>
        ),
      },
    ];
    return <TabExampleCustomMenuItem></TabExampleCustomMenuItem>;
  }
}
class AllUsers extends Component {
  constructor() {
    super();
    this.state = {
      users: [],
      error: [],
      open: false,
    };
  }

  componentDidMount() {
    list().then((data) => {
      if (data.error) {
        console.log(data.error);
      } else {
        this.setState({ users: data });
      }
    });
  }
  clickfollow = (user, i) => {
    const userId = isAuthenticated().user._id;
    const token = isAuthenticated().token;

    follow(userId, token, user._id).then((data) => {
      if (data.error) {
        this.setState({ error: data.error });
      } else {
        let toFollow = this.state.users;
        toFollow.splice(i, 1);
        this.setState({
          users: toFollow,
          open: true,
          followMessage: `Following ${user.name}`,
        });
      }
    });
  };
  /*   {users.map((user, i) => (
    <Header key={i}>
      {user.name}{" "}
    </Header>
    
  ))} */
  render() {
    const mystyle = {
      maxWidth: "13ch",
      display: "block",
      maxWidth: "30px",
      overFlow: "hidden",
      whiteSpace: "nowrap",
      textOverflow: "ellipsis",
    };
    const { users, open, followMessage } = this.state;

    return (
      <Container text style={{ marginTop: "10vh" }}>
        <Header as="h2" style={{ marginBottom: "18vh" }}>
          Users
          <p><div>{open && <p>{followMessage}</p>}</div></p>
        </Header>
        <Grid columns="three" divided>
          <Grid.Row>
            {users.map((user, i) => (
              <Grid.Column>
                <Card style={{ marginBottom: "5vh" }} key={i}>
                  <Image
                    src={`http://localhost/api/user/photo/${user._id}`}
                    onError={(i) => (i.target.src = `${DefaultProfile}`)}
                    rounded
                    style={{ minHeight: "200px", maxHeight: "200px" }}
                  />

                  <Card.Content>
                    <Card.Header>{user.name}</Card.Header>
                    <Card.Meta>
                      <span className="date">
                        Joined {new Date(user.created).toDateString()}
                      </span>
                    </Card.Meta>
                    <Card.Description>
                      <div className="ui bottom attached buttons">
                        <a href={`/user/${user._id}`}>
                          <button className="ui button">Profile</button>
                        </a>
                      </div>
                    </Card.Description>
                    <CardContent>
                      <span style={mystyle}> {user.about}</span>
                    </CardContent>
                  </Card.Content>
                  <Card.Content extra>
                    <a>
                      <Icon name="user" />
                      22 Friends
                    </a>
                  </Card.Content>
                </Card>
              </Grid.Column>
            ))}
          </Grid.Row>
        </Grid>
      </Container>
    );
  }
}

export default Users;
