import React, { Component } from "react";
import DefaultProfile from "./defauly.jpg";
import { isAuthenticated } from "../auth";
import {
  Container,
  Header,
  Grid,
  Image,
  Tab,
  Card,
  Icon,
  CardContent,
} from "semantic-ui-react";
import { list, findPeople } from "./apiuser";


class PeopleToFollow extends Component {
    constructor() {
      super();
      this.state = {
        users: [],
      };
    }
  
    componentDidMount() {
      const userId = isAuthenticated().user_id;
      const token = isAuthenticated().token;
      findPeople(userId, token).then((data) => {
        if (data.error) {
          console.log(data.error);
        } else {
          this.setState({ users: data });
        }
      });
    }
  
    /*   {users.map((user, i) => (
      <Header key={i}>
        {user.name}{" "}
      </Header>
      
    ))} */
    render() {
      const mystyle = {
        maxWidth: "13ch",
        display: "block",
        maxWidth: "30px",
        overFlow: "hidden",
        whiteSpace: "nowrap",
        textOverflow: "ellipsis",
      };
      const { users } = this.state;
      return (
        <Container text style={{ marginTop: "10vh" }}>
          <Header as="h2" style={{ marginBottom: "18vh" }}>
            Users
          </Header>
          <Grid columns="three" divided>
            <Grid.Row>
              {users.map((user, i) => (
                <Grid.Column>
                  <Card style={{ marginBottom: "5vh" }} key={i}>
                    <Image
                      src={`http://localhost/user/photo/${user._id}`}
                      onError={(i) => (i.target.src = `${DefaultProfile}`)}
                      rounded
                      style={{ minHeight: "200px", maxHeight: "200px" }}
                    />
  
                    <Card.Content>
                      <Card.Header>{user.name}</Card.Header>
                      <Card.Meta>
                        <span className="date">
                          Joined {new Date(user.created).toDateString()}
                        </span>
                      </Card.Meta>
                      <Card.Description>
                        <div class="ui bottom attached button">
                          <a href={`/user/${user._id}`}>
                            <button class="ui button">Profile</button>
                          </a>
                        </div>
                      </Card.Description>
                      <CardContent>
                        <span style={mystyle}> {user.about}</span>
                      </CardContent>
                    </Card.Content>
                    <Card.Content extra>
                      <a>
                        <Icon name="user" />
                        22 Friends
                      </a>
                    </Card.Content>
                  </Card>
                </Grid.Column>
              ))}
            </Grid.Row>
          </Grid>
        </Container>
      );
    }
  }
  export default PeopleToFollow